* {
  overflow-x: hidden;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.panel {
  
  background-color: white;
  display: flex;
  align-items: center;
  flex-direction: row;
  width: 100%;
  height: 60px;
  margin-top: 0px;
}
.menu{
  margin-left: 58%; 
  display: flex;
  align-items: baseline;
  flex-direction: row;
  max-width: fit-content;
}
/* */
.tab{
  margin: 0px 12px;
  font-family: 'Bahnschrift Light';
  font-size: 1em;
}
.gold{
  color: goldenrod;
}
.logo{
  margin-left: 80px;
}
.background{
  position: relative;
  width: 100vw;
  height: 60vh;
  display: flex;
  align-items: baseline;
  flex-direction: column;
  background-image: url("background.jpg");
  background-size: cover;
}
.left-aligned {
  margin-left: 15%;
}
.article-brand{
  text-align: left;
  margin-bottom: 10;
  margin-top: auto;
  color: white;
  font-family: 'Bahnschrift';
  font-size: 1em;
}
.article-title{
  margin-bottom: 10;
  color: white;
  /* font: 6em "Times New Roman"; */
  font-family: 'Bahnschrift Light';
  font-size: 48px;
  width: 40vw;
}
.article-authors{
  margin-bottom: 50px;
  justify-content: flex-end;
  color: white;
  font-family: 'Bahnschrift Light';
  font-size: 1em;
}
.label{
  margin-right: 50px;
  font-family: 'Bahnschrift Light';
  font-size: 1em;
}
.news{
  display: flex;
  flex-flow: row wrap;
  width: 45vw;
  height: auto;
  justify-content: space-between;
}
.sidebar{
  width: 23vw;
  height: auto;
  display: flex;
  flex-flow: column nowrap;
}
.rubric{
  width: 19vw;
  height: 6vh;
  border: 2px solid #d3d3d3;
  background-color: #fff;
}
.actual{
  width: 19vw;
  height: 50vh;
  margin-top: 3vh;
  border-left: 2px solid #d3d3d3;
  border-right: 2px solid #d3d3d3;
 
}
.m-t-9{
  margin-top: 9vh ;
}
.m-b-5{
  padding-left: 1px;
}
.wrapper{
  display: flex;
  gap: 70px;
  padding-top: 60px;
}
.post{
  width: 21vw;
  height: 55vh;
  margin-bottom: 5vh;
  border: 2px solid #d3d3d3;
}
.big-post{
  width: 100%;
  height: 70vh;
}
.post img{
  width: 100%;
  height: 55%;
  object-fit: cover;
}
.center-text{
  text-align: center;
  padding: 0vh 30px;
  line-height: 4vh;
}
.small-text{
  font-size: 0.7em;
}
.big-text{
  font-size: 1.3em;
}
.posts-text{
  padding-top: 1vh;
}
.left-text{
  margin-left: 4%;
  line-height: 4vh;
}
.info-text{
  font-size: 1em;
}
.pages{
  width: 100%;
  height: 7vh;
  margin-bottom: 5vh;
  margin-left:  15%;
  margin-right: 15%;
  border: 2px solid #d3d3d3;
}